@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(235, 235, 235);
}

code {
  font-family: 'Nunito', sans-serif;
}

.error {
    background-color: rgb(235, 235, 235);
    width: 25vw;
    min-width: 318px;
    padding: 1vw;
    margin: 20vh auto;
    margin-top: 20px;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    -webkit-transition: box-shadow 0.8s, background-color 0.8s;
    transition: box-shadow 0.8s, background-color 0.8s;
    box-sizing: border-box;
    text-align: center;
}

.error:hover {
    box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    background-color: rgb(248, 248, 248);
}
header {
    position: absolute;
    display: flex;
    flex-direction: row;
    background-color: rgb(235, 235, 235);
    height:9vh;
    top: 20px;
    right: 0;
    left: 0;
    margin: 0 20px;
    border-radius: 50px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    z-index: 1;
    -webkit-transition: box-shadow 0.8s, background-color 0.8s;
    transition: box-shadow 0.8s, background-color 0.8s;
  }

  header:hover {
    box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    background-color: rgb(248, 248, 248);
  }
  
  header h1 {
    line-height: 0;
    margin: auto 40px;
    font-size: 1.5rem;
  }
.form {
    position: relative;
    margin: 17vh auto;
    margin-bottom: 0;
    width: 25vw;
    min-width: 318px;
    background-color: rgb(235, 235, 235);
    text-align: center;
    padding: 10px 0;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    -webkit-transition: box-shadow 0.8s, background-color 0.8s;
    transition: box-shadow 0.8s, background-color 0.8s;
}

.form:hover {
    box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    background-color: rgb(248, 248, 248);
}

.input {
    font-family: 'Nunito', sans-serif;
    margin: 0 10px;
    padding-left: 5px;
    border: none;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3) inset, -2px -2px 5px rgb(255, 255, 255) inset;
    -webkit-transition: box-shadow 0.8s, background-color 0.8s;
    transition: box-shadow 0.8s, background-color 0.8s;
}

.form:hover > .input {
    box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.3) inset, -2px -2px 5px rgb(255, 255, 255) inset;
}

.button {
    font-family: 'Nunito', sans-serif;
    border: none;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    -webkit-transition: box-shadow 0.8s, background-color 0.8s;
    transition: box-shadow 0.8s, background-color 0.8s;
}

.form:hover > .button {
    box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.3), -2px -2px 5px rgb(255, 255, 255);
}
.card {
    background-color: rgb(235, 235, 235);
    width: 25vw;
    min-width: 318px;
    padding: 1vw;
    margin: 0 auto;
    margin-bottom: 20px;
    margin-top: 20px;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    -webkit-transition: box-shadow 0.8s, background-color 0.8s;
    transition: box-shadow 0.8s, background-color 0.8s;
    box-sizing: border-box;
}

.card:hover {
    box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.3), -3px -3px 5px rgb(255, 255, 255);
    background-color: rgb(248, 248, 248);
}

.card:hover > p {
    box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.3) inset, -3px -3px 5px rgb(255, 255, 255) inset;
}

.profile-img {
    height: 23vw;
    width: 23vw;
    margin: 0 auto;
    min-height: 290px;
    min-width: 290px;
    background-repeat: no-repeat;
    background-size: contain;
}

.card h2 {
    margin: 8px 0;
}

.card p {
    margin-top: 0;
    background-color: transparent;
    border-radius: 20px;
    padding: 5px;
    padding-left: 20px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3) inset, -3px -3px 5px rgb(255, 255, 255) inset;
    -webkit-transition: box-shadow 0.8s, background-color 0.8s;
    transition: box-shadow 0.8s, background-color 0.8s;
}

.last-p {
    margin-bottom: 0;
    padding-bottom: 0;
}

.profile-link {
    margin-top: 15px;
    color: black;
    -webkit-transition: letter-spacing 1s;
    transition: letter-spacing 1s;
}

.profile-link:hover {
    letter-spacing: 1px;
}

.flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
